import { createApp } from 'vue';
import router from './';
import app from './app.vue';

// Vue Router$(() => {})
const vueApp = createApp(app);
vueApp.use(router);

// 
const perfStartTime = performance.now();

// 
const preloadCriticalRoutes = async () => {
  try {
    // 
    const criticalRoutes = ['/service', '/history'];
    await Promise.all(
      criticalRoutes.map(path => {
        const route = router.resolve(path);
        // Vue Router
        return route;
      })
    );
    console.log('[] ');
  } catch (error) {
    console.warn('[] :', error);
  }
};

// 
const preloadCriticalResources = async () => {
  try {
    // 
    const resources = [
      // 
    ];

    await Promise.all(
      resources.map(url => {
        return new Promise((resolve, reject) => {
          const link = document.createElement('link');
          link.rel = 'prefetch';
          link.href = url;
          link.onload = resolve;
          link.onerror = reject;
          document.head.appendChild(link);
        });
      })
    );
    console.log('[] ');
  } catch (error) {
    console.warn('[] :', error);
  }
};

// MVU
const waitForMvu = async (timeout = 5000) => {
  return Promise.race([
    waitGlobalInitialized('Mvu'),
    new Promise((_, reject) =>
      setTimeout(() => reject(new Error('MVU')), timeout)
    ),
  ]).catch(err => {
    console.warn('[MVU] :', err);
    return null; // null
  });
};

// 
const initApp = async () => {
  try {
    // 
    const initTasks = Promise.all([
      waitForMvu(),                    // MVU
      preloadCriticalRoutes(),         // 
      preloadCriticalResources(),      // 
    ]);

    // 
    const [mvuReady] = await initTasks;

    // 
    const initTime = performance.now() - perfStartTime;
    console.log(`[] : ${initTime.toFixed(2)}ms`);

    // MVU
    if (mvuReady) {
      toastr.success(`(${initTime.toFixed(0)}ms)`, '');
    } else {
      toastr.warning('', '');
    }

    // Vue
    vueApp.mount('#app');

    // 
    const mountTime = performance.now() - perfStartTime;
    console.log(`[] : ${mountTime.toFixed(2)}ms`);

  } catch (error) {
    console.error('[] :', error);
    toastr.error('', '');
  }
};

//  jQuery  DOMContentLoaded
$(() => {
  //  - VueonMounted
  initApp();
});

